                    console.log('📡 [DECK RIGHT] Chiamata updateStream con stream:', !!newMixedStream)
                    const updated = await streamingManager.updateStream(newMixedStream)
                    console.log('📡 [DECK RIGHT] updateStream risultato:', updated)
                    
